/**
 * @param {PlainObject} cfg
 * @param {DiscordClient} cfg.client
 * @returns {BotCommands}
 */
const getBahaiSalutations = ({client}) => {
  return {
    // GREETINGS //
    abha: {
      re: /\b(?:all[a]h['-]?[uo]?['-]?abh[a]|abh[a])/iu,
      /**
       * Allh-u-Abh!
       * @param {DiscordMessage} message
       * @returns {void}
       */
      action (message) {
        const star = client.emojis.cache.find((val) => val.name === '9star');
        message.react(star);
        message.channel.send(
          `Allh-u-Abh, ${message.author.username}! ${star.toString()}`
        );
      },
      notMentioned: {
        /**
         * If welcome AND a user are mentioned.
         * @param {DiscordMessage} message
         * @returns {void}
         */
        check (message) {
          return Boolean(message.mentions.members.first());
        },
        /**
         * @param {DiscordMessage} message
         * @returns {void}
         */
        action (message) {
          const star = client.emojis.cache.find((val) => val.name === '9star');
          message.react(star);
        }
      }
    },
    nawruz: {
      re: /\b(?:(?:happy|joyous)\s?n[ao]w[- ]?ro?[u]z|n[ao]w[- ]?ro?[u]z\s?(?:m[uo]b[a]r[a]k))\b/iu,
      /**
       * @param {DiscordMessage} message
       * @returns {void}
       */
      action (message) {
        const star = client.emojis.cache.find((val) => val.name === '9star');
        const sabz = client.emojis.cache.find((val) => val.name === 'sabzi');
        message.react(star);
        message.channel.send(
          `Happy Naw-Rz! ${star.toString()} ${sabz.toString()}`
        );
      }
    },
    ridvan: {
      re: /\b(?:(?:happy|joyous)\s?r[ie][dz][vw][a]n|r[ie][dz][vw][a]n\s?(?:m[uo]b[a]r[a]k))\b/iu,
      /**
       * @param {DiscordMessage} message
       * @returns {void}
       */
      action (message) {
        const star = client.emojis.cache.find((val) => val.name === '9star');
        message.react(star);
        message.channel.send(`Happy Ridvn! ${star.toString()}`);
      }
    }
  };
};

export default getBahaiSalutations;
